import React, { useState, useEffect, useRef } from "react";
import { Container, Input, Button } from "reactstrap";
import Matter from 'matter-js';
import 'pathseg';

export function SplashScreen({ onFileUpload, onStartGame, showEditor, publicUrl }) {
    const [message, setMessage] = useState("Upload a file to begin");
    const [buttonState, setButtonState] = useState(false);
    const sceneRef = useRef(null);

    const onFileUploadOverride = (e) => {
        if (e.target.files.length === 0) {
            setMessage('No file selected');
            setButtonState(false);
            return;
        }
        setMessage('Press "Play Game" to start');
        setButtonState(true);
        onFileUpload(e);
    };

    useEffect(() => {
        const createSvg = async (letter, scale) => {
            const response = await fetch(`${process.env.PUBLIC_URL}/letterSVG/${letter}.svg`);
            const { Vertices } = Matter;
            console.log(`${process.env.PUBLIC_URL}/letterSVG/${letter}.svg`);
            const rawSvg = await response.text();
            const parser = new DOMParser();
            const svgDoc = parser.parseFromString(rawSvg, 'image/svg+xml');
            var path = svgDoc.querySelector('path');
            console.log(path);
            if (path) {
                return Vertices.scale(Matter.Svg.pathToVertices(path, 30), scale, scale);
            }
            console.error('No path found in SVG');
            return [];
        };

        const addLettersToWorld = async () => {
            const { Engine, Render, Runner, Bodies, Composite, World, Common, Body } = Matter;
            const engine = Matter.Engine.create();
            engine.world.gravity.y = 1;
            engine.world.gravity.x = 0;
            const world = engine.world;
            Common.setDecomp(require('poly-decomp'));

            const render = Render.create({
                element: sceneRef.current,
                engine: engine,
                options: {
                    width: window.innerWidth,
                    height: window.innerHeight,
                    wireframes: false,
                    background: 'transparent'
                }
            });

            function getRandomHexColor() {

                const min = 0;
                const max = 200;

                const r = Math.floor(Math.random() * (max - min) + min);
                const g = Math.floor(Math.random() * (max - min) + min);
                const b = Math.floor(Math.random() * (max - min) + min);

                const toHex = (n) => n.toString(16).padStart(2, '0');

                const hexColor = `${toHex(r)}${toHex(g)}${toHex(b)}`;

                return hexColor;
            }



            const welcomeMessageText = 'Welcome to School Feud';
            for (let i = 0; i < welcomeMessageText.length; i++) {
                var random_color = getRandomHexColor();
                random_color = random_color.slice(0, 5);
                random_color = random_color + 'f';
                var x = window.innerWidth * 0.25 + (i * window.innerWidth / 18);
                //calculate the scale of the letter to ensure it fits correctly in the physics world on many screen sizes, also calculate the distance between letters to ensure they are not overlapping
                var y = window.innerHeight * 0.2;
                if (i >= 11) {
                    y = window.innerHeight * 0.3;
                    x = window.innerWidth * 0.25 + ((i - 11) * window.innerWidth / 18);
                }
                var scale = window.innerWidth / (1080 * 2);
                var customLetterScale = scale * 1.8;
                if (welcomeMessageText[i] === ' ') continue;
                if (welcomeMessageText[i] === 'o') {
                    // add the "o" as two circles stacked
                    var compoundBody = Body.create({
                        parts: [

                            Bodies.circle(x, y, 30 * customLetterScale, {
                                render: {
                                    fillStyle: "#" + random_color,
                                    strokeStyle: "#" + random_color,
                                    lineWidth: 1,
                                },
                                friction: 0.5,
                                frictionStatic: 0.5,
                                restitution: 0.25,
                                density: 1000,
                            }),
                            Bodies.circle(x, y, 15 * customLetterScale, {
                                render: {
                                    fillStyle: 'white',
                                    strokeStyle: 'white',
                                    lineWidth: 1,
                                },
                                isStatic: false
                            })
                        ]
                    });
                    Composite.add(world, [compoundBody]);
                    continue;
                } else if (welcomeMessageText[i] === 'd') {
                    // add the "d" as a rectangle and a half circle
                    compoundBody = Body.create({
                        parts: [
                            Bodies.rectangle(x, y, 30 * customLetterScale, 60 * customLetterScale, {
                                render: {
                                    fillStyle: "#" + random_color,
                                    strokeStyle: "#" + random_color,
                                    lineWidth: 1,
                                },
                                friction: 0.5,
                                frictionStatic: 0.5,
                                restitution: 0.25,
                                density: 1000,
                            }),
                            Bodies.circle(x + 20 * customLetterScale, y, 30 * customLetterScale, {
                                render: {
                                    fillStyle: "#" + random_color,
                                    strokeStyle: "#" + random_color,
                                    lineWidth: 1,
                                },
                                friction: 0.5,
                                frictionStatic: 0.5,
                                restitution: 0.25,
                                density: 1000,
                                isStatic: false,
                                angle: Math.PI / 2
                            }),
                            //add in a white circle to be the 'inside' of the 'd', and use a rectangle to cover part of the circle
                            Bodies.rectangle(x + 10 * customLetterScale, y, 15 * customLetterScale, 30 * customLetterScale, {
                                render: {
                                    fillStyle: 'white',
                                    strokeStyle: 'white',
                                    lineWidth: 1,
                                },
                                friction: 0.5,
                                frictionStatic: 0.5,
                                restitution: 0.25,
                                density: 1000
                            }),
                            Bodies.circle(x + 20 * customLetterScale, y, 15 * customLetterScale, {
                                render: {
                                    fillStyle: 'white',
                                    strokeStyle: 'white',
                                    lineWidth: 1,
                                },
                                friction: 0.5,
                                frictionStatic: 0.5,
                                restitution: 0.25,
                                density: 1000,
                                isStatic: false,
                                angle: Math.PI / 2
                            }),
                        ]
                    });
                    Composite.add(world, [compoundBody]);
                    continue;
                }

                const vertices = await createSvg(welcomeMessageText[i].toLowerCase(), scale);
                // var internal = welcomeMessageText[i].toLowerCase() !== 'o' || welcomeMessageText[i].toLowerCase() !== 'd';
                if (vertices.length > 0) {
                    const welcomeMessageLetter = Bodies.fromVertices(x, y, [vertices], {
                        render: {
                            //pick a random color using math.random and hex values
                            fillStyle: '#' + random_color,
                            strokeStyle: '#' + random_color,
                            lineWidth: 1,
                        },
                        friction: 0.5,
                        frictionStatic: 0.5,
                        restitution: 0.25,
                        density: 1000
                    });
                    if (welcomeMessageLetter === undefined) {
                        console.log('undefined letter');
                    }
                    else {
                        World.add(world, [welcomeMessageLetter]);
                    }
                }
            }

            const ground = Bodies.rectangle(window.innerWidth / 2, window.innerHeight * 0.4, window.innerWidth, 60, {
                isStatic: true,
                render: { fillStyle: 'transparent' }
            });
            const leftWall = Bodies.rectangle(0, window.innerHeight * 0.4, 60, window.innerHeight, {
                isStatic: true,
                render: { fillStyle: 'transparent' }
            });
            const rightWall = Bodies.rectangle(window.innerWidth, window.innerHeight * 0.4, 60, window.innerHeight, {
                isStatic: true,
                render: { fillStyle: 'transparent' }
            });
            const ceiling = Bodies.rectangle(window.innerWidth / 2, 0, window.innerWidth, 60, {
                isStatic: true,
                render: { fillStyle: 'transparent' }
            });


            //add mouse control
            const mouse = Matter.Mouse.create(render.canvas);
            const mouseConstraint = Matter.MouseConstraint.create(engine, {
                mouse: mouse,
                constraint: {
                    stiffness: 0.2,
                    render: { visible: false }
                }
            });

            World.add(world, mouseConstraint);

            render.mouse = mouse;

            World.add(world, [ground, leftWall, rightWall, ceiling]);
            //check to make sure that no letters are outside of the bounds of the screen, if they are, move them back in

            Matter.Events.on(engine, 'beforeUpdate', function (event) {
                var bodies = Composite.allBodies(engine.world);

                //get the dims of the physics world
                var y = render.bounds.max.y;
                var x = render.bounds.max.x;
                //loop through all of the bodies in the physics world, ensure that they are within the bounds of the screen
                //if they are off the screen, remove all motion and drop them in the center of the screen
                for (var i = 0; i < bodies.length; i++) {
                    var body = bodies[i];
                    if (body.position.y > y || body.position.y < 0 || body.position.x > x || body.position.x < 0) {
                        console.log('out of bounds');
                        Body.setVelocity(body, { x: 0, y: 0 });
                        Body.setPosition(body, { x: 50, y: 50 });
                    }
                }


            }
            );
            engine.world.gravity.y = 0;
            Render.run(render);
            const runner = Runner.create();
            Runner.run(runner, engine);

            setInterval(() => {
                engine.world.gravity.y = 1
                engine.world.gravity.x = 0
            }, 2000);

            return () => {
                Render.stop(render);
                Runner.stop(runner);
                World.clear(world);
                Engine.clear(engine);
                render.canvas.remove();
                //remove the svg elements from the simulation


            };
        };

        addLettersToWorld();

    }, [publicUrl]);

    return (
        <Container className="text-center" style={{ paddingTop: '20%' }}>
            <Button onClick={showEditor} style={{ position: 'absolute', right: '10px', top: '10px', zIndex: 100 }}>
                Question Editor
            </Button>
            <Input type="file" onChange={onFileUploadOverride} style={{
                zIndex: 100, position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', width: '50%'

            }} accept=".csv" />
            <h2 style={{
                zIndex: 100, position: 'absolute', top: '40%', left: '50%', transform: 'translate(-50%, -50%)'
            }}>
                {message}

            </h2>
            <Button color="success" size="lg" onClick={onStartGame} style={{
                animation: buttonState ? 'pulse 1s infinite' : 'none',
                zIndex: 100,
                position: 'absolute', top: '60%', left: '50%', transform: 'translate(-50%, -50%)'
            }} disabled={!buttonState}>
                Play Game
            </Button>
            <div ref={sceneRef} style={{
                position: 'absolute', top: 0, left: 0, width: '100%', height: '40%', zIndex: 1, overflow: 'hidden'
            }}></div>
        </Container>
    );
}
