import './App.css';
import React, { useState, useRef, useEffect } from 'react';
import { Container, Row, Col, Table, Button } from 'reactstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Papa from 'papaparse';
import { SplashScreen } from './splashscreen';
import { CSVEditor } from './editor';
import { TweenLite, TweenMax, Power3, Back } from 'gsap';
import { gsap } from 'gsap';
import { CSSPlugin } from 'gsap/CSSPlugin';

gsap.registerPlugin(CSSPlugin);

function App() {
    const maxStrikes = 3;
    const [team1Strikes, setTeam1Strikes] = useState(0);
    const [team2Strikes, setTeam2Strikes] = useState(0);
    const [question, setQuestion] = useState('Please upload a game file to begin');
    const [questionIndex, setQuestionIndex] = useState(0);
    const [answers, setAnswers] = useState([]);
    const [answerValues, setAnswerValues] = useState([]);
    const [team1AnswerIndeces, setTeam1AnswerIndeces] = useState([]);
    const [team2AnswerIndeces, setTeam2AnswerIndeces] = useState([]);
    const [currentTeam, setCurrentTeam] = useState(1);
    const [numberOfQuestions, setNumberOfQuestions] = useState(0);
    const [parsedResults, setParsedResults] = useState([]);
    const [popoutVisbility, setPopoutVisibility] = useState(false);
    const popoutWindow = useRef(null);

    const [showSplashScreen, setShowSplashScreen] = useState(true);
    const [showEditor, setShowEditor] = useState(false);

    const renderStrikes = (team) => {
        let renderStrikes = [];
        renderStrikes.push(
            <Button key={`-1 - ${team}`} onClick={() => {
                if (team === 0) {
                    if (team1Strikes > 0) {
                        setTeam1Strikes(team1Strikes - 1);
                    }
                }
                else {
                    if (team2Strikes > 0) {
                        setTeam2Strikes(team2Strikes - 1);
                    }
                }
            }}
                disabled={team === 0 ? team1Strikes === 0 : team2Strikes === 0}
            >
                -
            </Button>)
        for (let i = 0; i < maxStrikes; i++) {
            renderStrikes.push(
                <Button key={`${i} - ${team}`} style={{
                    backgroundColor: team === 0 ? team1Strikes > i ? 'red' : 'white' : team2Strikes > i ? 'red' : 'white', width: '100px', height: '100px'

                }} onClick={() => {

                }}>
                    <h1 style={{
                        fontSize: '3em', fontWeight: 'bold', visibility: team === 0 ? team1Strikes > i ? 'visible' : 'hidden' : team2Strikes > i ? 'visible' : 'hidden'
                    }}>
                        X
                    </h1>
                </Button>
            )
        }
        renderStrikes.push(
            <Button key={`+1 - ${team}`} onClick={() => {
                if (team === 0) {
                    if (team1Strikes < maxStrikes) {
                        setTeam1Strikes(team1Strikes + 1);
                    }
                }
                else {
                    if (team2Strikes < maxStrikes) {
                        setTeam2Strikes(team2Strikes + 1);
                    }
                }
            }}
                disabled={team === 0 ? team1Strikes === maxStrikes : team2Strikes === maxStrikes}
            >
                +
            </Button>)
        return renderStrikes;
    };

    const handleFileUpload = (event) => {
        const file = event.target.files[0];
        Papa.parse(file, {
            header: false,
            complete: (results) => {
                const data = results.data[0];
                setQuestion(data[0]);
                const loadedAnswers = [];
                const loadedAnswerValues = [];
                for (let i = 1; i < data.length; i += 2) {
                    if (data[i] && data[i + 1]) {
                        loadedAnswers.push(data[i]);
                        loadedAnswerValues.push(parseInt(data[i + 1], 10));
                    }
                }
                setAnswers(loadedAnswers);
                setAnswerValues(loadedAnswerValues);
                setNumberOfQuestions(results.data.length);
                setParsedResults(results.data); // Store the parsed results
                setQuestionIndex(0);
                setTeam1AnswerIndeces([]);
                setTeam2AnswerIndeces([]);
                updateContent(0);
            },
        });
    };

    const loadQuestionByIndex = (index) => {
        console.log('loading question', index);
        if (index >= 0 && index < numberOfQuestions) {
            const data = parsedResults[index];
            setQuestion(data[0]);
            const loadedAnswers = [];
            const loadedAnswerValues = [];
            for (let i = 1; i < data.length; i += 2) {
                if (data[i] && data[i + 1]) {
                    loadedAnswers.push(data[i]);
                    loadedAnswerValues.push(parseInt(data[i + 1], 10));
                }
            }
            setAnswers(loadedAnswers);
            setAnswerValues(loadedAnswerValues);
            setQuestionIndex(index);
            setTeam1AnswerIndeces([]);
            setTeam2AnswerIndeces([]);
            updateContent(index);
        }
    };


    const popOutTeacherPanel = () => {
        setPopoutVisibility(true);
        if (popoutWindow.current && !popoutWindow.current.closed) {
            popoutWindow.current.focus();
            return;
        }
        //clear the current popout window
        popoutWindow.current = window.open("", "Teacher Panel", "width=600,height=400");
        if (!popoutWindow.current) {
            alert('Please enable popups for this site');
            return;
        }
        popoutWindow.current.document.write(`
        <html>
            <head>
                <title>Teacher Panel</title>
                <style>
                    body { font-family: Arial, sans-serif; margin: 20px; position: relative; }
                    h2 { font-size: 24px; }
                    ul { list-style-type: none; padding: 0; }
                    li { margin: 5px 0; }
                    button { padding: 10px; font-size: 16px; }
                    #content.blur { filter: blur(10px); }
                    .overlay {
                        position: fixed;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        background-color: rgba(0, 0, 0, 0.5);
                        color: white;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 24px;
                        z-index: 10;
                    }
                </style>
            </head>
            <body>
                <div id="content" class="blur">
                </div>
                <div id="overlay" class="overlay" onclick="document.getElementById('content').classList.remove('blur'); document.getElementById('overlay').style.display='none';"> 
                Click to unblur
                </div>
                <button onclick="window.close()">Close</button>

            </body>
        </html>
    `);

        const content = popoutWindow.current.document.getElementById('content');
        const questionElement = popoutWindow.current.document.createElement('h2');
        questionElement.textContent = question;
        content.appendChild(questionElement);

        const listElement = popoutWindow.current.document.createElement('ul');
        answers.forEach((answer, index) => {
            const listItem = popoutWindow.current.document.createElement('li');
            listItem.textContent = `${answer}: ${answerValues[index]} points`;
            listElement.appendChild(listItem);
        });
        content.appendChild(listElement);
        popoutWindow.current.onbeforeunload = () => {
            setPopoutVisibility(false);
        };
    };

    const updateContent = (index) => {
        if (!popoutWindow.current || popoutWindow.current.closed) {
            return;
        }
        const data = parsedResults[index];
        console.log('updating content', index, data);
        if (data) {
            const questionElement = popoutWindow.current.document.createElement('h2');
            questionElement.textContent = index + 1 + '. ' + data[0];
            const content = popoutWindow.current.document.getElementById('content');
            content.innerHTML = ''; // Clear previous content
            content.appendChild(questionElement);

            const listElement = popoutWindow.current.document.createElement('ul');
            for (let i = 1; i < data.length; i += 2) {
                if (data[i] && data[i + 1]) {
                    const listItem = popoutWindow.current.document.createElement('li');
                    listItem.textContent = `${data[i]}: ${parseInt(data[i + 1], 10)} points`;
                    listElement.appendChild(listItem);
                }
            }
            content.appendChild(listElement);
            //append the question index to the content so we can use it later
        }
    };

    const selectAnswer = (team, answerIndex) => {
        const cardElement = document.querySelector(`.cardHolder[data-index='${answerIndex}']`);
        if (cardElement) {
            flipCard(cardElement, false); // Flip to back
        }
        if (team === 1) {
            setTeam1AnswerIndeces([...team1AnswerIndeces, answerIndex]);
            setCurrentTeam(0);
        } else {
            setTeam2AnswerIndeces([...team2AnswerIndeces, answerIndex]);
            setCurrentTeam(1);
        }

    };

    const deselectAnswer = (answerIndex) => {
        const cardElement = document.querySelector(`.cardHolder[data-index='${answerIndex}']`);
        if (cardElement) {
            flipCard(cardElement, true); // Flip to front
        }
        setTeam1AnswerIndeces(team1AnswerIndeces.filter(index => index !== answerIndex));
        setTeam2AnswerIndeces(team2AnswerIndeces.filter(index => index !== answerIndex));

    };

    const flipCard = (cardElement, isFlippingToBack) => {
        const card = cardElement.querySelector('.card1');
        const backs = cardElement.querySelector('.back');
        const cardSides = cardElement.querySelector('.front');
        console.log(cardElement, card, backs, cardSides);
        const rotation = isFlippingToBack ? 0 : -180;
        TweenLite.set(cardElement, { perspective: 800 });
        TweenLite.set(card, { transformStyle: "preserve-3d" });
        TweenLite.set(backs, { rotationX: 180 });
        TweenLite.set(cardSides, { backfaceVisibility: "hidden" });
        TweenLite.to(card, 1, { rotationX: rotation, ease: Back.easeOut });

    };

    const renderTeams = () => {
        const team1Points = team1AnswerIndeces.reduce((acc, index) => acc + answerValues[index], 0);
        const team2Points = team2AnswerIndeces.reduce((acc, index) => acc + answerValues[index], 0);
        let curWinningTeam = team1Points > team2Points ? 1 : 0;
        const glowStyleTeam0 = {
            position: 'relative',
            boxShadow: 'none',
            width: '500px',
        };
        const glowStyleTeam1 = {
            position: 'relative',
            boxShadow: 'none',
            width: '500px',

        };

        if (curWinningTeam === 1) {
            glowStyleTeam1.boxShadow = '0 0 20px yellow';
        }

        if (curWinningTeam === 0) {
            glowStyleTeam0.boxShadow = '0 0 20px yellow';
        }
        if (team1Points === team2Points && team1Points + team2Points !== 0) {
            glowStyleTeam1.boxShadow = '0 0 20px yellow';
            glowStyleTeam0.boxShadow = '0 0 20px yellow';
        }

        if (team1Points + team2Points === 0) {
            glowStyleTeam1.boxShadow = 'none';
            glowStyleTeam0.boxShadow = 'none';
        }

        return (
            <Row className="justify-content-between align-items-center mt-4" xs="2">
                <Col className="text-center teamHolder" style={{ backgroundColor: currentTeam === 1 ? 'rgba(0, 190, 240, 0.5)' : 'rgba(190,190,190,0.5)', position: 'relative', zIndex: 1, ...glowStyleTeam1 }} onClick={() => setCurrentTeam(1)}>
                    <h2>Team 1: <br />{team1AnswerIndeces.reduce((acc, index) => acc + answerValues[index], 0)} Points</h2>
                    {renderStrikes(0)}
                </Col>
                <Col className="text-center teamHolder" style={{ backgroundColor: currentTeam === 0 ? 'rgba(0, 190, 240, 0.5)' : 'rgba(190,190,190,0.5)', position: 'relative', zIndex: 1, ...glowStyleTeam0 }} onClick={() => setCurrentTeam(0)}>
                    <h2>Team 2:<br />{team2AnswerIndeces.reduce((acc, index) => acc + answerValues[index], 0)} Points</h2>
                    {renderStrikes(1)}
                </Col>
            </Row>
        );
    }

    const toggleAnswer = (team, index) => {
        if (team1AnswerIndeces.concat(team2AnswerIndeces).includes(index)) {
            deselectAnswer(index);
        }
        else {
            selectAnswer(team, index);
        }
    }

    const boardScoreRef = useRef(null);
    const team1ScoreRef = useRef(null);
    const team2ScoreRef = useRef(null);

    const renderGame = () => {
        return (
            <div className='game'>
                <Button onClick={popOutTeacherPanel} style={{ position: 'absolute', right: '10px', top: '10px' }} disabled={popoutVisbility}>
                    Teacher Panel
                </Button>

                <div className='gameBoard'>
                    <Row className="justify-content-center" style={{ margin: 'auto' }}>
                        <h1 className="mt-4"><strong>School Feud</strong></h1>
                    </Row>
                    <Row className="justify-content-center">
                        <Col xs="auto">
                            <Button onClick={() => loadQuestionByIndex(questionIndex - 1)} disabled={questionIndex === 0}>Previous Question</Button>
                        </Col>
                        <Col xs="auto">
                            <Button onClick={() => loadQuestionByIndex(questionIndex + 1)} disabled={questionIndex === numberOfQuestions - 1}>Next Question</Button>
                        </Col>
                    </Row>
                    {renderTeams()}

                    <div className='questionHolder'>
                        <span className='question'>{question}</span>
                    </div>

                    <div className='colHolder' style={{ boxSizing: 'content-box' }}>
                        <div className='collom1' style={{ boxSizing: 'content-box' }}>
                            {answers.slice(0, Math.ceil(answers.length / 2)).map((answer, index) => (
                                <div className='cardHolder' key={index} data-index={index} style={{ boxSizing: 'content-box' }}>
                                    <div className='card1' onClick={() => toggleAnswer(currentTeam, index)} style={{ boxSizing: 'content-box' }}>
                                        <div className='front' style={{ visibility: !team1AnswerIndeces.concat(team2AnswerIndeces).includes(index) ? 'visible' : 'hidden' }}>
                                            <span className='DBG'>{index + 1}</span>
                                        </div>
                                        <div className='back DBG' style={{ transform: 'rotateX(180deg)', boxSizing: 'content-box', visibility: team1AnswerIndeces.concat(team2AnswerIndeces).includes(index) ? 'visible' : 'hidden' }}>
                                            <span>{answer}</span>
                                            <b className='LBG'>{answerValues[index]}</b>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                        <div className='collom2' style={{ boxSizing: 'content-box' }}>
                            {answers.slice(Math.ceil(answers.length / 2)).map((answer, index) => (
                                <div className='cardHolder' key={index + Math.ceil(answers.length / 2)} data-index={index + Math.ceil(answers.length / 2)} style={{ boxSizing: 'content-box' }}>
                                    <div className='card1' onClick={() => toggleAnswer(currentTeam, index + Math.ceil(answers.length / 2))} style={{ boxSizing: 'content-box' }}>
                                        <div className='front' style={{ visibility: !team1AnswerIndeces.concat(team2AnswerIndeces).includes(index + Math.ceil(answers.length / 2)) ? 'visible' : 'hidden' }}>
                                            <span className='DBG'>{(index + Math.ceil(answers.length / 2)) + 1}</span>
                                        </div>
                                        <div className='back DBG' style={{ transform: 'rotateX(180deg)', boxSizing: 'content-box', visibility: team1AnswerIndeces.concat(team2AnswerIndeces).includes(index + Math.ceil(answers.length / 2)) ? 'visible' : 'hidden' }}>
                                            <span>{answer}</span>
                                            <b className='LBG'>{answerValues[index + Math.ceil(answers.length / 2)]}</b>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        )
    }


    useEffect(() => {
        // Initial setup for scores
        if (boardScoreRef.current) boardScoreRef.current.textContent = '0';
        if (team1ScoreRef.current) team1ScoreRef.current.textContent = '0';
        if (team2ScoreRef.current) team2ScoreRef.current.textContent = '0';
    }, []);




    const showEditorOverride = () => {
        setShowEditor(true);
        setShowSplashScreen(false);
    }

    const closeEditorOverride = () => {
        setShowEditor(false);
        setShowSplashScreen(true);
    }

    return (
        <span>
            {showEditor && (
                <CSVEditor closeEditor={() => closeEditorOverride()} />
            )}
            {showSplashScreen && (
                <span style={{ overflow: "hidden" }}>

                    <SplashScreen
                        onFileUpload={(results) => {
                            handleFileUpload(results);
                        }}
                        onStartGame={() => setShowSplashScreen(false)}
                        showEditor={() => showEditorOverride()}
                    />
                </span>
            )}
            {showSplashScreen || showEditor ? null :
                renderGame()
            }
        </span>
    );
}

export default App;
